import log from 'loglevel'

export default (vectorTiles) => {
    log.info('VECTORTILES', vectorTiles)
    const sources = Object.keys(vectorTiles).reduce((acc, vkey) => {
        const vlayer = vectorTiles[vkey]
        return {
            ...acc,
            [vlayer.sourceName]: {
                tiles: [vlayer.url],
                type: 'vector',
                minzoom: vlayer.minZoom,
                maxzoom: vlayer.maxZoom,
                bounds: vlayer.bounds
            }
        }
    }, {})

    const layers = [
        {
            id: 'NHDFlowlines_lines',
            type: 'line',
            source: vectorTiles.NHDFlowline.sourceName,
            'source-layer': 'NHDFlowlines',
            minzoom: vectorTiles.NHDFlowline.minZoom,
            maxzoom: 16.5,
            layout: { visibility: 'visible' },
            paint: {
                'line-opacity': ['interpolate', ['linear'], ['zoom'], vectorTiles.NHDFlowline.minZoom, 0, 8, 1],
                'line-width': ['interpolate', ['linear'], ['zoom'], vectorTiles.NHDFlowline.minZoom, 0.5, 16, 3],
                'line-color': [
                    'match',
                    ['get', 'NHDPlusID'],
                    [-1],
                    '#FFFF00',
                    ['step', ['get', 'FType'], '#FF0000', 460, '#0000FF']
                ]
            }
        }
    ]
    return {
        version: 8,
        name: 'Outdoors',
        metadata: {
            'mapbox:type': 'default',
            'mapbox:origin': 'outdoors-v11',
            'mapbox:autocomposite': true,
            'mapbox:groups': {},
            'mapbox:sdk-support': { js: '0.50.0', android: '6.7.0', ios: '4.6.0' }
        },
        // 'center': [ -114.19115411710368, 43.32991248939766 ],
        // 'zoom': 9.061958904803715,
        // 'bearing': 0,
        // 'pitch': 0,
        sources: { ...sources },
        sprite: 'mapbox://sprites/pipbailey/cjvfq2uwm0vqk1foby0dtwdmk/ayuihl2d38ufkjhmu0pwx0cto',
        glyphs: 'mapbox://fonts/pipbailey/{fontstack}/{range}.pbf',
        layers: [...layers],
        created: '2019-05-08T21:16:06.811Z',
        id: 'cjvfq2uwm0vqk1foby0dtwdmk',
        modified: '2019-05-08T22:02:34.939Z',
        owner: 'pipbailey',
        visibility: 'private',
        draft: false
    }
}
