/* eslint-disable @typescript-eslint/camelcase */
import React, { useCallback, useState } from 'react'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import MetricAccordion from './MetricAccordion'
import MetricModal from './MetricModal'
import { OfflineStateType, MetricPrefsType, FieldDef } from '../types'
import GetAppIcon from '@material-ui/icons/GetApp'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles, Theme, StyleRules } from '@material-ui/core/styles'
import log from 'loglevel'

interface Props {
    fieldDefs: FieldDef[]
    loading: boolean
    metrics?: any
    nhdProps?: any
    metricPrefs: MetricPrefsType
    setOfflineState: (partialState: Partial<OfflineStateType>) => void
}

const useStyles = makeStyles(
    (theme: Theme): StyleRules => ({
        root: {
            height: '100%',
            overflow: 'hidden',
            '& *': {
                userSelect: 'text'
            },
            display: 'flex',
            flexDirection: 'column'
        },
        title: {
            flexGrow: 1
        }
    })
)

const MarkerFields: React.FC<Props> = ({
    metrics,
    loading,
    nhdProps,
    fieldDefs,
    metricPrefs,
    setOfflineState
}: Props) => {
    const classes = useStyles()
    const [downloadDlg, setDownloadDlg] = useState<boolean>(false)

    const handleAccordionToggle = useCallback(
        (gname: string): void => {
            const newAccordion = { ...metricPrefs.accordion }
            setOfflineState({
                metricPrefs: {
                    ...metricPrefs,
                    accordion: { ...newAccordion, [gname]: !Boolean(newAccordion[gname]) }
                }
            })
        },
        [metricPrefs, setOfflineState]
    )

    if (loading) return <Typography>Loading...</Typography>
    log.debug('MarkerFields::', { metrics, loading, nhdProps, fieldDefs, metricPrefs })
    const combinedMetrics = { ...metrics.catch_metrics, ...metrics.wing_metrics }
    // const defLookup = fieldDefs.reduce((acc, fd) => ({ ...acc, [fd.metric_key]: fd }), {})
    const groupNames = Array.from(new Set(fieldDefs.map((d) => d.group_name)))
    const fieldKeys = Array.from(new Set(fieldDefs.map((d) => d.metric_key)))

    // We filter out anything that's already pinned
    const groups = groupNames.map((gname) => ({
        name: gname,
        fields: fieldDefs.map((def) => ({
            def,
            val: combinedMetrics === null ? null : combinedMetrics[def.metric_key]
        }))
    }))

    // Sort names alphabetically
    Object.keys(groups).forEach((gname) => {
        groups[gname].fields.sort((a, b) => {
            return a.def.metric_name.toLowerCase().localeCompare(b.def.metric_name.toLowerCase())
        })
    })

    const handleTogglePin = (pinToggle: string): void => {
        const isPinned = metricPrefs.pinned && metricPrefs.pinned.indexOf(pinToggle) > -1
        const newPinned = metricPrefs.pinned || []
        setOfflineState({
            metricPrefs: {
                ...metricPrefs,
                pinned: isPinned ? newPinned.filter((p) => p !== pinToggle) : [...newPinned, pinToggle]
            }
        })
    }
    // For now push in any metrics we don't use and the nhd fields
    // TODO: this needs to go away in production
    groups.push({
        name: 'Other',
        fields: combinedMetrics
            ? Object.keys(combinedMetrics)
                  .filter((mname) => fieldKeys.indexOf(mname) === -1)
                  .map((n) => nhdFieldMaker(n, nhdProps[n]))
            : []
    })
    groups.push({
        name: 'NHD Fields',
        fields: Object.keys(nhdProps).map((n) => nhdFieldMaker(n, nhdProps[n]))
    })

    const pinned = metricPrefs.pinned
        ? metricPrefs.pinned.map((pname) => ({
              def: fieldDefs.find((fd) => fd.metric_key === pname) || nhdFieldMaker(pname, combinedMetrics[pname]).def,
              val: combinedMetrics[pname] || nhdProps[pname]
          }))
        : []

    const groupsFiltered = groups.map(({ name, fields }) => ({
        name,
        fields: fields.filter((fld) => metricPrefs.pinned.indexOf(fld.def.metric_key) === -1)
    }))

    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Toolbar>
                    <Typography variant="subtitle1" className={classes.title}>
                        NHDPlusID: {nhdProps.nhdplusid}
                    </Typography>
                    <IconButton className={classes.dwnldBtn} color="inherit" onClick={(): void => setDownloadDlg(true)}>
                        <GetAppIcon />
                    </IconButton>
                </Toolbar>
            </AppBar>
            <MetricAccordion
                groups={groupsFiltered}
                pinned={pinned}
                metricPrefs={metricPrefs}
                onPinToggle={handleTogglePin}
                onAccordionToggle={handleAccordionToggle}
            />
            <MetricModal data={groups} open={downloadDlg} handleClose={(): void => setDownloadDlg(false)} />
        </div>
    )
}

export default MarkerFields

const NHDLookup = {
    spheroid_dist: {
        group_name: 'NHD Fields',
        metric_key: 'spheroid_dist',
        metric_type: 'float',
        metric_name: 'Distance to Reach',
        description: 'Distance from the point you clicked to the nearest point on the reach.',
        units: 'm',
        format_string: '0,0.0'
    },
    fcode: {
        group_name: 'NHD Fields',
        metric_key: 'fcode',
        metric_type: 'id',
        metric_name: 'FCode',
        description: null,
        units: null,
        format_string: null
    }
}

export function nhdFieldMaker(name: string, val: any): any {
    return NHDLookup[name]
        ? { def: NHDLookup[name], val: val }
        : {
              def: {
                  group_name: 'NHD Fields',
                  metric_key: name,
                  metric_name: name,
                  description: null,
                  units: null,
                  format_string: null
              },
              val
          }
}
