/* eslint-disable @typescript-eslint/camelcase */
import React, { useState } from 'react'
import clsx from 'clsx'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/Tooltip'
import numeral from 'numeral'

import CancelOutlinedIcon from '@material-ui/icons/CancelOutlined'
import TurnedInNotSharpIcon from '@material-ui/icons/TurnedInNotSharp'

import { FieldDef } from '../types'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles, Theme, StyleRules } from '@material-ui/core/styles'
// import log from 'loglevel'

import { util } from '@northarrowresearch/common'

interface Props {
    def: FieldDef
    val: any
    isPinned?: boolean
    onPinToggle: (pinToggle: string) => void
}

function dataPrint(metric_type: string, metric_key: string, format_string: string, val: any): React.ReactNode {
    if (val === null) {
        return <em>NULL</em>
    } else if (metric_type) {
        switch (metric_type) {
            case 'string':
                return val
            case 'id':
                return val
            case 'float':
                if (!util.isNumeric(val)) {
                    return val
                } else if (format_string && format_string.length > 0) {
                    return numeral(val).format(format_string)
                } else {
                    return util.formatNum(val, 3)
                }
            default:
                return val
        }
    }
    // Generic, unlisted values
    else if (util.isNumeric(val)) {
        if (metric_key === 'spheroid_dist') {
            return util.formatNum(val, 6)
        }
        return util.formatNum(val, 3)
    } else {
        return val.toString()
    }
}

// This is all here for some hacky nhdprops fields
const IDS = [
    'nhdplusid',
    'vpuid',
    'reachcode',
    'permanent_identifier',
    'fromnode',
    'tonode',
    'hydroseq',
    'levelpathi',
    'terminalpa',
    'dnlevelpat',
    'dnhydroseq'
]

const useStyles = makeStyles(
    (theme: Theme): StyleRules => ({
        root: {
            display: 'flex',
            width: '100%',
            overflow: 'hidden',
            borderBottom: '1px solid #DEDEDE'
        },
        rootHover: {
            background: '#eee'
        },
        inner: {
            display: 'flex',
            width: '100%',
            overflow: 'hidden'
            // '& > *': {
            //     border: '1px solid green'
            // }
        },
        noval: {
            color: '#999999'
        },
        pinned: {
            background: theme.palette.secondary.main,
            borderBottom: `1px solid ${theme.palette.secondary.main}`,
            color: theme.palette.secondary.contrastText
        },
        name: {
            padding: '3px',
            flex: '1 1 70%',
            textAlign: 'left',
            whiteSpace: 'nowrap',
            overflow: 'hidden',
            textOverflow: 'ellipsis'
        },
        value: {
            lineHeight: '1.5rem',
            padding: '3px',
            flex: '1 1 20%',
            textAlign: 'right'
        },
        idVal: {
            color: theme.palette.text.secondary,
            padding: theme.spacing(0.5, 1),
            border: '1px solid #dddddd',
            background: '#eeeeee'
        },
        stringVal: {
            textAlign: 'left'
        },
        unit: {
            lineHeight: '1.5rem',
            padding: '3px',
            paddingLeft: theme.spacing(1) / 2,
            flex: '0 1 5%',
            textAlign: 'left'
        },
        pinButton: {
            opacity: 0
        },
        buttonHover: {
            flex: '0 0',
            opacity: 1
        }
    })
)

const MetricRow: React.FC<Props> = ({ def, val, isPinned, onPinToggle }: Props) => {
    const classes = useStyles()
    const [isHovered, setHovered] = useState<boolean>(false)
    const deftype = def.metric_type ? def.metric_type : IDS.indexOf(def.metric_key) > -1 ? 'id' : 'unknown'
    const valstr = val === undefined ? ' ' : dataPrint(deftype, def.metric_key, def.format_string, val)
    return (
        <div
            className={clsx(classes.root, { [classes.pinned]: isPinned, [classes.rootHover]: !isPinned && isHovered })}
            onMouseOver={(): void => setHovered(true)}
            onMouseOut={(): void => setHovered(false)}
            onDoubleClick={(): void => onPinToggle(def.metric_key)}
        >
            <Tooltip
                title={
                    <div>
                        <Typography variant="body1">
                            {def.metric_name}
                            {def.units ? ` (${def.units})` : ''}
                        </Typography>
                        {def.description && <Typography variant="body2">{def.description}</Typography>}
                    </div>
                }
            >
                <div
                    className={clsx(classes.inner, {
                        [classes.noval]: !isPinned && (val === undefined || val === null)
                    })}
                >
                    <Typography variant="body1" className={classes.name}>
                        {def.metric_name}
                    </Typography>
                    {/* <Typography variant="overline" color="primary">
                        {deftype}
                    </Typography> */}
                    <div className={classes.value}>
                        <Typography
                            variant={deftype === 'id' || val === undefined ? 'overline' : 'body2'}
                            className={clsx({
                                [classes.idVal]: val !== undefined && deftype === 'id',
                                [classes.stringVal]: val !== undefined && deftype === 'string'
                            })}
                        >
                            {valstr}
                        </Typography>
                    </div>
                    <Typography variant="caption" className={classes.unit}>
                        {val === undefined ? '' : def.units}
                    </Typography>
                </div>
            </Tooltip>

            <Tooltip title={isPinned ? 'Unpin this metric' : 'Pin this metric'}>
                <IconButton
                    size="small"
                    className={clsx(classes.pinButton, { [classes.buttonHover]: isHovered })}
                    onClick={(): void => onPinToggle(def.metric_key)}
                    color="inherit"
                >
                    {isPinned ? <CancelOutlinedIcon /> : <TurnedInNotSharpIcon />}
                </IconButton>
            </Tooltip>
        </div>
    )
}

export default MetricRow
