import { useCallback, useEffect } from 'react'
import { OfflineStateType } from './types'
import { initialStateDefaults, version } from './config'
import log from 'loglevel'
import { useLocalStorage } from '@northarrowresearch/react-common'

const STATE_KEY = 'INIT_STATE'

const useInitialState = (): [any, (partialState: Partial<OfflineStateType>) => void] => {
    const [initState, _setInitState] = useLocalStorage(STATE_KEY, initialStateDefaults)

    // This ensures a clean state when there's a version upgrade. Maybe a little excessive but irt
    // will really help with debugging
    useEffect(() => {
        if (version !== initState.version) {
            log.warn('Version upgrade detected, wiping slate!')
            _setInitState(initialStateDefaults)
        }
    }, [initState.version, _setInitState])

    const setInitialState = useCallback(
        (partialState: Partial<OfflineStateType>): void => {
            _setInitState({
                ...initState,
                ...partialState
            } as OfflineStateType)
        },
        [initState, _setInitState]
    )

    return [initState, setInitialState]
}

export default useInitialState
