import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import { AppFrame } from '@northarrowresearch/react-common'
import Topbar from './Topbar'
import Map from './Map'
import { OfflineStateType, EphemeralStateType } from './types'
import MetricView from './MetricView'
import style from './App.style'
import { Paper, Typography } from '@material-ui/core'

const useStyles = makeStyles(style)

interface AppProps {
    error?: string
    offlineState: OfflineStateType
    setOfflineState: (partialState: Partial<OfflineStateType>) => void
    ephemeralState: EphemeralStateType
    searchSubmit: (nhdid: number) => Promise<void>
    handleMapClick: (e: any, MapboxObj: any) => void
    signOut: () => void
}

/**
 * This is the over-arching wrapper for the app. It combines all the
 * necessary pieces
 */
const App: React.FC<AppProps> = ({
    error,
    offlineState,
    setOfflineState,
    ephemeralState,
    handleMapClick,
    searchSubmit,
    signOut
}: AppProps) => {
    const classes = useStyles()

    return (
        <AppFrame
            className={classes.appFrame}
            topbar={<Topbar title={'Network Attributes'} searchSubmit={searchSubmit} signOut={signOut} />}
            fullWidth
            noScroll
        >
            <div className={classes.parentContainer}>
                {error && (
                    <Paper>
                        <Typography color="error" variant="body2">
                            {error}
                        </Typography>
                    </Paper>
                )}
                {!error && ephemeralState && ephemeralState.selectedReach && (
                    <div id="drawer-container" className={classes.drawerContainer}>
                        <MetricView
                            {...{
                                loading: ephemeralState.selectedReach.loading,
                                metrics: ephemeralState.selectedReach?.metrics,
                                nhdProps: ephemeralState.selectedReach?.nhdProps,
                                fieldDefs: ephemeralState.fieldDefs.defs || [],
                                metricPrefs: offlineState.metricPrefs,
                                setOfflineState
                            }}
                        />
                    </div>
                )}
                {!error && (
                    <div className={classes.mapContainer}>
                        <Map
                            {...{
                                initialViewport: offlineState.viewport,
                                vectorTiles: ephemeralState.vectorTileDefs.tiles,
                                selectedReach: ephemeralState.selectedReach,
                                handleMapClick
                            }}
                        />
                    </div>
                )}
            </div>
        </AppFrame>
    )
}

export default App
