import React, { useState, useEffect, useRef, useCallback } from 'react'
import PropTypes from 'prop-types'
import useInitialState from './App.state'
import { useHosted } from '@northarrowresearch/react-auth'
import { api, graphql } from '@netatt/common'
import App from './App'
import log from 'loglevel'
import { endpoints, aws, isDev } from './config'
import { EphemeralStateType, OfflineStateType } from './types'

const defaultCurrentState = {
    fieldDefs: { loading: true },
    vectorTileDefs: { loading: true }
}

export type StateRefType = {
    ephemeral: EphemeralStateType
    offline: OfflineStateType
}

const AppConnect: React.FC = () => {
    // This is the main loading. It needs the field defs AND the vector tiles to be considered false
    const [loading, setLoading] = useState<boolean>(true)
    const [error, setError] = useState<string>()
    const [offlineState, setOfflineState] = useInitialState()
    const [ephemeralState, setEphemeralState] = useState<EphemeralStateType>(defaultCurrentState)

    const { isAuthenticated, getCurrentToken, signIn, signOut } = useHosted(aws, true, isDev)
    // Below is here in case we need to turn authentication off for some reason
    // const getCurrentToken = useCallback((): Promise<string> => Promise.resolve(''), [])
    // const signOut = (): void => console.log('SIGN OUT)

    // Store our state in a ref so we can access it without page reloads
    const stateRef = useRef<StateRefType>()
    stateRef.current = { offline: offlineState, ephemeral: ephemeralState }

    // Recalculate the loading indicator for us
    useEffect(() => {
        const newLoading = !ephemeralState || ephemeralState.fieldDefs.loading || ephemeralState.vectorTileDefs.loading
        if (newLoading !== loading) setLoading(newLoading)
    }, [ephemeralState, loading])

    const setPartialEphemeralState = useCallback(
        (partial: Partial<EphemeralStateType>): void => {
            setEphemeralState({
                ...stateRef.current?.ephemeral,
                ...partial
            } as EphemeralStateType)
        },
        [setEphemeralState]
    )

    // Fetch our base layers
    useEffect(() => {
        // RS TILES CDN ==> /NET_ATT/BLM/NHDFlowline/metadata.json
        if (!stateRef.current?.ephemeral.vectorTileDefs.loading) return
        const tilepath = 'NET_ATT/BLM/NHDFlowline'
        const url = `${process.env.REACT_APP_RS_TILES_CDN}/${tilepath}/metadata.json`
        log.info('URL', url)

        fetch(url)
            .then((res) => res.json())
            .then((d) => {
                d.bounds = JSON.parse(`[${d.bounds}]`)
                d.center = JSON.parse(`[${d.center}]`)
                return {
                    ...d,
                    sourceName: 'NHDFlowline',
                    url: `${process.env.REACT_APP_RS_TILES_CDN}/${tilepath}/{z}/{x}/{y}.pbf`,
                    json: JSON.parse(d.json),
                    zoom: 14,
                    minZoom: parseInt(d.minzoom, 10),
                    maxZoom: parseInt(d.maxzoom, 10),
                    longitude: d.center[0],
                    latitude: d.center[1]
                }
            })
            .then((NHDFlowline) => {
                log.debug('RESP', NHDFlowline)
                setPartialEphemeralState({
                    vectorTileDefs: { tiles: { NHDFlowline }, loading: false }
                })
            })
            .catch((err) => {
                log.error('DATA::fetch_tile::err:', err) // Print the error if one occurred
                setError('Error fetching vector tile definitions')
                setPartialEphemeralState({
                    vectorTileDefs: { tiles: undefined, loading: false }
                })
            })
    }, [setPartialEphemeralState])

    useEffect(() => {
        if (!isAuthenticated) return
        getCurrentToken().then((jwt) =>
            api
                .apiCall(endpoints.graphql as string, jwt, graphql.queries.fieldDefs, {})
                .then((response: any) => {
                    if (response.data.fieldDefs) {
                        setPartialEphemeralState({
                            fieldDefs: { defs: JSON.parse(response.data.fieldDefs), loading: false }
                        })
                    }
                })
                .catch((err) => {
                    log.error('DATA::fetch_tile::err:', err) // Print the error if one occurred
                    setError('Error fetching field definitions')
                    setPartialEphemeralState({
                        fieldDefs: { defs: undefined, loading: false }
                    })
                })
        )
    }, [getCurrentToken, setPartialEphemeralState, isAuthenticated])

    const handleGotRecord = useCallback(
        (record: any): void => {
            try {
                log.debug('handleGotRecord:: Record', record)
                const { cp_line_pt: cpLinePt, pt, ...nhdProps } = JSON.parse(record.nhdProps)
                const metrics = record.metrics ? JSON.parse(record.metrics) : {}
                log.debug('handleGotRecord:: Record data parsed', { cpLinePt, pt, nhdProps })
                const newPt = cpLinePt ? JSON.parse(cpLinePt) : undefined

                setPartialEphemeralState({
                    selectedReach: {
                        ...stateRef.current?.ephemeral.selectedReach,
                        loading: false,
                        intersect: newPt ? newPt.coordinates : undefined,
                        catchGeo: record.catchGeom.bbox ? JSON.parse(record.catchGeom.geom) : undefined,
                        catchBBox: record.catchGeom.bbox ? JSON.parse(record.catchGeom.bbox) : undefined,
                        reachBBox: record.reachGeom.bbox ? JSON.parse(record.reachGeom.bbox) : undefined,
                        metrics,
                        nhdProps
                    }
                })
            } catch (err) {
                log.error('DATA::handleGotRecord::err:', { err, record }) // Print the error if one occurred
                setError('Error parsing record from database')
                setPartialEphemeralState({
                    selectedReach: { ...stateRef.current?.ephemeral.selectedReach, loading: false }
                })
            }
        },
        [setPartialEphemeralState]
    )

    const handleMapClick = useCallback(
        ({ lngLat }): Promise<void> => {
            if (!isAuthenticated) return Promise.resolve()
            setPartialEphemeralState({
                selectedReach: {
                    ...stateRef.current?.ephemeral.selectedReach,
                    clickCoords: lngLat,
                    loading: true,
                    intersect: undefined
                }
            })
            return getCurrentToken().then((jwt) =>
                api
                    .apiCall(endpoints.graphql as string, jwt, graphql.queries.geoRecord, { lngLat })
                    .then((response: any) => handleGotRecord(response.data.geoRecord))
            )
        },
        [getCurrentToken, setPartialEphemeralState, isAuthenticated, handleGotRecord]
    )

    const handleSearchSubmit = useCallback(
        (nhdplusid: number): Promise<void> => {
            if (!isAuthenticated) return Promise.resolve()
            return getCurrentToken().then((jwt) =>
                api
                    .apiCall(endpoints.graphql as string, jwt, graphql.queries.record, { id: nhdplusid })
                    .then((response: any) => {
                        if (response.data.record) {
                            return handleGotRecord(response.data.record)
                        } else {
                            throw new Error('response.data.record not found')
                        }
                    })
                    .catch((err) => {
                        log.error('DATA::handleSearchSubmit::err:', { err }) // Print the error if one occurred
                        setError('Error retrieving record from database')
                        setPartialEphemeralState({
                            selectedReach: { ...stateRef.current?.ephemeral.selectedReach, loading: false }
                        })
                    })
            )
        },
        [getCurrentToken, handleGotRecord, setPartialEphemeralState, isAuthenticated]
    )
    // Set up an initial search if we have one in the localstorage
    useEffect(() => {
        if (isAuthenticated && !stateRef.current?.ephemeral.selectedReach && offlineState.selectedReachId) {
            handleSearchSubmit(offlineState.selectedReachId)
        }
    }, [offlineState, handleSearchSubmit, isAuthenticated])

    if (!isAuthenticated)
        return (
            <div>
                <button onClick={(): Promise<any> => signIn()}>Logged Out</button>
            </div>
        )

    return (
        <App
            {...{
                error,
                offlineState,
                setOfflineState,
                ephemeralState,
                handleMapClick,
                searchSubmit: handleSearchSubmit,
                signOut
            }}
        />
    )
}
AppConnect.propTypes = {
    location: PropTypes.object,
    history: PropTypes.object
}

export default AppConnect
