import React, { useState } from 'react'
import Toolbar from '@material-ui/core/Toolbar'
import AppBar from '@material-ui/core/AppBar'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import InputBase from '@material-ui/core/InputBase'
import { util } from '@northarrowresearch/common'
import clsx from 'clsx'
// import MenuIcon from '@material-ui/core/Menu'
import { makeStyles } from '@material-ui/core/styles'
import SearchIcon from '@material-ui/icons/Search'
import style from './Topbar.style'
import { version } from './config'
import log from 'loglevel'
const useStyles = makeStyles(style)

interface Props {
    title: string
    searchSubmit: (nhdid: number) => Promise<void>
    signOut: () => void
}
type SearchState = {
    error?: string
    text: string
}
const defaultState = { text: '' }

const TopBar: React.FC<Props> = ({ title, searchSubmit, signOut }: Props) => {
    const classes = useStyles()
    const [searchState, setSearchState] = useState<SearchState>(defaultState)

    const searchSubmitHandle = (nhdid: string): void => {
        if (nhdid && nhdid.length > 0 && util.isNumeric(nhdid)) {
            setSearchState({ ...searchState, error: undefined })
            log.debug('searchSubmitHandle called', nhdid)
            searchSubmit(parseInt(nhdid, 10)).then(() => {
                setSearchState(defaultState)
            })
        } else {
            log.debug('searchSubmitHandle ERROR non numeric')
            setSearchState({ ...searchState, error: 'Non-Numberic nhdid' })
        }
    }

    return (
        <React.Fragment>
            <AppBar position="static">
                <Toolbar className={classes.root} disableGutters>
                    <a href="http://riverscapes.xyz/">
                        <img
                            className={classes.logo}
                            src={'https://riverscapes.xyz/assets/images/RiverscapesLogo.png'}
                            alt="logo"
                        />
                    </a>
                    <Typography variant="subtitle1">{title}</Typography>
                    <Typography variant="overline" className={classes.version}>
                        v.{version}
                    </Typography>
                    <div className={classes.spacer} />
                    {searchState.error && (
                        <Typography variant="subtitle1" color="error">
                            {searchState.error}
                        </Typography>
                    )}
                    <div className={clsx(classes.search, { [classes.searchError]: Boolean(searchState.error) })}>
                        <div className={classes.searchIcon}>
                            <SearchIcon />
                        </div>
                        <InputBase
                            placeholder="NHDPlusIDâ€¦ (55000600125036)"
                            onKeyDown={(event): void => {
                                if (event.key === 'Enter') {
                                    log.debug('searchSubmitHandle ENTER DETECTED', event)
                                    searchSubmitHandle(searchState.text)
                                }
                            }}
                            value={searchState.text}
                            onChange={(e): void => setSearchState({ ...searchState, text: e.target.value })}
                            classes={{
                                root: classes.inputRoot,
                                input: classes.inputInput
                            }}
                            inputProps={{ 'aria-label': 'search' }}
                        />
                    </div>
                    <Button color="inherit" onClick={(): void => signOut()}>
                        Sign Out
                    </Button>
                </Toolbar>
            </AppBar>
        </React.Fragment>
    )
}

export default TopBar
