import React from 'react'
import MuiAccordion from '@material-ui/core/Accordion'
import MuiAccordionSummary from '@material-ui/core/AccordionSummary'
import MuiAccordionDetails from '@material-ui/core/AccordionDetails'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import Typography from '@material-ui/core/Typography'

import MetricRow from './MetricRow'

import { MetricPrefsType } from '../types'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles, Theme, StyleRules } from '@material-ui/core/styles'
// import log from 'loglevel'

interface Props {
    groups: any[]
    pinned: any[]
    onPinToggle: (pinToggle: string) => void
    onAccordionToggle: (gname: string) => void
    metricPrefs: MetricPrefsType
}

const useStyles = makeStyles(
    (theme: Theme): StyleRules => ({
        root: {
            overflow: 'scroll'
        },
        accordion: {
            border: '1px solid rgba(0, 0, 0, .125)',
            boxShadow: 'none',
            '&:not(:last-child)': {
                borderBottom: 0
            },
            '&:before': {
                display: 'none'
            },
            '&$expanded': {
                margin: 'auto'
            }
        },
        accordionExpanded: {},
        summary: {
            display: 'flex'
        },
        summaryExpanded: {
            // '&$expanded': {
            //     margin: '12px 0'
            // }
        },
        summaryContent: {
            '&$expanded': {
                margin: '12px 0'
            }
        },
        details: {
            display: 'flex',
            flexDirection: 'column',
            padding: theme.spacing(1, 1, 1, 2)
        },
        fieldTitle: {
            flexGrow: 1
        },
        fieldCount: {}
    })
)

const MetricAccordion: React.FC<Props> = ({ groups, pinned, onPinToggle, metricPrefs, onAccordionToggle }: Props) => {
    const classes = useStyles()

    return (
        <div className={classes.root}>
            {pinned.map(({ def, val }, idp) => (
                <MetricRow key={`mrow-pinned-${idp}`} def={def} val={val} isPinned onPinToggle={onPinToggle} />
            ))}
            {groups.map(({ name, fields }, idx) => (
                <MuiAccordion
                    key={`accordion-${idx}`}
                    square
                    classes={{
                        root: classes.accordion,
                        expanded: classes.accordionExpanded
                    }}
                    expanded={Boolean(metricPrefs.accordion && metricPrefs.accordion[name])}
                    onChange={(): void => onAccordionToggle(name)}
                >
                    <MuiAccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        classes={{
                            root: classes.summary,
                            expanded: classes.summaryExpanded,
                            content: classes.summaryContent
                        }}
                    >
                        <Typography className={classes.fieldTitle}>{name}</Typography>
                        <Typography className={classes.fieldCount} variant="caption">
                            {fields.reduce((acc, f) => {
                                return f.val ? acc + 1 : acc
                            }, 0)}
                            /{fields.length}
                        </Typography>
                    </MuiAccordionSummary>
                    <MuiAccordionDetails className={classes.details}>
                        {fields
                            .filter((f) => !metricPrefs.pinned || metricPrefs.pinned.indexOf(f.metric_key) === -1)
                            .map(({ def, val }, idt) => (
                                <MetricRow key={`mrow-${idx}-${idt}`} def={def} val={val} onPinToggle={onPinToggle} />
                            ))}
                    </MuiAccordionDetails>
                </MuiAccordion>
            ))}
        </div>
    )
}
export default MetricAccordion
