/* eslint-disable @typescript-eslint/camelcase */
import React, { useState, useRef, useEffect, useMemo, useCallback } from 'react'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
// import DialogContentText from '@material-ui/core/DialogContentText'
// import DialogTitle from '@material-ui/core/DialogTitle'
import TextField from '@material-ui/core/TextField'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'
import MuiAlert from '@material-ui/lab/Alert'
import Snackbar from '@material-ui/core/Snackbar'

import copy from 'clipboard-copy'
// import Typography from '@material-ui/core/Typography'

import { makeStyles, Theme, StyleRules } from '@material-ui/core/styles'
// import log from 'loglevel'

// import { util } from '@northarrowresearch/common'

interface Props {
    data: any
    open: boolean
    handleClose: () => void
}

const useStyles = makeStyles(
    (theme: Theme): StyleRules => ({
        root: {
            display: 'flex',
            width: '100%',
            overflow: 'hidden'
        },
        tabs: {},
        dlgContent: {
            height: '100%',
            display: 'flex',
            flexDirection: 'column'
        },
        codeContainer: {
            padding: theme.spacing(1, 0.5),
            minWidth: '60hw',
            overflow: 'hidden'
        },
        code: {
            color: '#cccccc',
            background: '#444444'
            // whiteSpace: 'pre-wrap'
        },
        inputText: {
            padding: theme.spacing(1, 0.5),
            fontFamily: 'Courier New,Courier,Lucida Sans Typewriter,Lucida Typewriter,monospace',
            color: '#cccccc'
        }
    })
)

enum TabList {
    JSON,
    CSV
}

const MetricRow: React.FC<Props> = ({ data, open, handleClose }: Props) => {
    const classes = useStyles()
    const inputRef = useRef<HTMLInputElement>()
    const [tab, setTab] = useState<TabList>(TabList.JSON)
    const [snack, setSnack] = useState<boolean>(false)

    const renderText = useMemo(() => {
        let text = ''
        const reducedText = data.reduce(
            (acc, grp) => ({
                ...acc,
                [grp.name]: grp.fields.reduce((acc, { def, val }) => ({ ...acc, [def.metric_name]: val }), {})
            }),
            {}
        )
        switch (tab) {
            case TabList.JSON:
                text = JSON.stringify(reducedText, null, 4)
                break
            default:
                text = Object.keys(reducedText)
                    .reduce(
                        (acc, grpname) => [
                            ...acc,
                            ...Object.keys(reducedText[grpname]).map(
                                (kname) =>
                                    `${grpname},${kname},${
                                        reducedText[grpname][kname] ? reducedText[grpname][kname] : ''
                                    }`
                            )
                        ],
                        ['Group,Name,Value']
                    )
                    .join('\r\n')
                break
        }
        return text
    }, [data, tab])

    const handleFocus = useCallback((): void => {
        if (open && inputRef.current) {
            inputRef.current.select()
        }
    }, [open])

    useEffect(() => {
        if (!open) return
        copy(renderText)
        setSnack(true)
    }, [open, renderText])

    return (
        <>
            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title" maxWidth="md" fullWidth>
                <DialogContent className={classes.dlgContent}>
                    <Tabs
                        className={classes.tabs}
                        value={tab}
                        onChange={(event, newValue): void => setTab(newValue)}
                        aria-label="Data selector tabs"
                    >
                        <Tab label="JSON" value={TabList.JSON} />
                        <Tab label="CSV" value={TabList.CSV} />
                    </Tabs>

                    <Box className={classes.codeContainer}>
                        <TextField
                            multiline
                            fullWidth
                            inputRef={inputRef}
                            inputProps={{
                                className: classes.inputText,
                                readOnly: true,
                                wrap: 'soft'
                            }}
                            className={classes.code}
                            rows={10}
                            onFocus={handleFocus}
                            onSelect={handleFocus}
                            onChange={handleFocus}
                            onClick={(): void => {
                                copy(renderText)
                                setSnack(true)
                            }}
                            aria-label="maximum height"
                            placeholder="Maximum 4 rows"
                            value={renderText}
                        />
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Cancel
                    </Button>
                </DialogActions>
            </Dialog>
            <Snackbar
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'center'
                }}
                color="secondary"
                open={snack}
                autoHideDuration={3000}
                onClose={(): void => setSnack(false)}
            >
                <MuiAlert elevation={6} variant="filled" severity="info">
                    {tab === TabList.JSON && 'JSON '}
                    {tab === TabList.CSV && 'CSV '}
                    Copied to clipboard
                </MuiAlert>
            </Snackbar>
        </>
    )
}

export default MetricRow
